@media screen and (max-width: 600px) {
  .FlatList {
    display: grid;
    grid-template-columns: 100%;
    grid-auto-flow: row;
    padding-bottom: 3%;
  }
}

/* styles for devices with a minimum width of 601px and a maximum width of 1200px */
@media screen and (min-width: 601px) and (max-width: 1200px) {
  .FlatList {
    padding-top: 5%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-auto-flow: row;
    padding-bottom: 3%;
  }
}

/* styles for devices with a minimum width of 1201px */
@media screen and (min-width: 1201px) {
  .FlatList {
    padding-top: 5%;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-auto-flow: row;
    padding-bottom: 3%;
  }
}

:root {
  --border-color: #394867;
  --background-color: #f1f6f9;
  --highlight: #212a3e;
  --mid: #9ba4b5;
}

html {
  background-color: var(--background-color);
}

.HeaderComponent {
  background-color: var(--mid);
  font-size: calc(10px + 2vmin);
  color: var(--background-color);
  text-align: center;
  padding-top: 1%;
  padding-bottom: 1%;
  border: solid var(--border-color) 2px;
}

.SingleFlat {
  border: solid var(--border-color) 2px;
  border-radius: 20px;
  margin: 5%;
  background-color: var(--background-color);
  text-align: center;
}

.SingleFlat p {
  color: var(--highlight);
  padding-left: 1%;
}
.SingleFlat a {
  color: var(--highlight);
  padding-left: 1%;
  text-decoration: none;
}

.SingleFlat a:hover {
  color: var(--mid);
}

h1 {
  color: var(--border-color);
  text-align: center;
}
select {
  border: solid var(--border-color) 2px;
  border-radius: 20px;
  margin: 5%;
  background-color: var(--background-color);
  text-align: center;
  color: var(--highlight);
  padding: 1%;
  margin-left: 1%;
}
select:hover {
  background-color: var(--mid);
  color: var(--background-color);
  cursor: pointer;
}

.Dropdown {
  color: var(--border-color);
  text-align: center;
}

.FlatName {
  align-content: center;
  text-decoration: underline;
}

.FlatDetail {
  text-align: left;
}
